import * as React from "react";
import * as marked from "marked";

/**
 * デモコンテンツの情報
 */
export interface ContentData {
	src: string;
	width: number;
	height: number;
}

/**
 * デモコンテンツ表示用フレームのデザインの情報
 */
export interface ContentFrameStyle {
	width: number;
	height: number;
	margin: string;
}

export interface SampleDemoComponentProps {
	caption: string;
	description: string;
	contentData: ContentData;
}

export interface SampleDemoComponentState {
	// いわゆる container (状態を持つ) ではない component だが、contentFrameStyleはこの component のデザインに関する情報なので、state として保持する
	contentFrameStyle: ContentFrameStyle;
}

/**
 * デモコンテンツ表示用コンポーネント
 */
export class SampleDemoComponent extends React.Component<SampleDemoComponentProps, SampleDemoComponentState> {
	private contentFrameDomNode: HTMLElement|null = null;

	constructor(props: SampleDemoComponentProps) {
		super(props);
		// コンテンツ表示用フレームの位置・スケールの本当の値はDOMツリーができた後でないと算出できないため、DOMツリーができるまでは仮の値でstateを初期化する
		this.state = {
			contentFrameStyle: {
				width: this.props.contentData.width,
				height: this.props.contentData.height,
				margin: "0px auto"
			}
		};
	}

	componentDidMount() {
		// stateの実質の初期化
		this.setState({contentFrameStyle: this.getContentFrameStyle()});

		window.addEventListener("resize", () => {
			if (this.contentFrameDomNode === null) {
				return;
			}
			// コンテンツの方がリサイズに追従するコードになっておらず、ロード時にしかサイズの調整をしないため、リサイズ時にリロードする
			const iframeDomNode = this.contentFrameDomNode.getElementsByTagName("iframe")[0];
			if (iframeDomNode.contentDocument !== null) {
				iframeDomNode.contentDocument.location.reload(true);
			}

			this.setState({contentFrameStyle: this.getContentFrameStyle()});
		});
	}

	render() {
		return <section className="asset--head">
			<div className="responsive--width cfix">
				<div className="asset--head--demo safearea--LR" ref={ node => this.contentFrameDomNode = node }>
					<div style={ this.state.contentFrameStyle }>
						<iframe className="asset-demo-frame" src={ this.props.contentData.src }></iframe>
					</div>
				</div>
				<div className="asset--head--info safearea--LR">
					<h1>{ this.props.caption }</h1>
					<div dangerouslySetInnerHTML={ {__html: marked(this.props.description)} } />
				</div>
			</div>
		</section>;
	}

	private getContentFrameStyle(): ContentFrameStyle {
		let width = this.props.contentData.width;
		let height = this.props.contentData.height;
		let heightMargin = 0;
		if (this.contentFrameDomNode !== null) {
			const widthRatio = this.contentFrameDomNode.offsetWidth / width;
			const heightRatio = this.contentFrameDomNode.offsetHeight / height;
			const ratio = Math.min(widthRatio, heightRatio);
			if (ratio < 1) {
				width = Math.round(width * ratio);
				height = Math.round(height * ratio);
			}
			heightMargin = (this.contentFrameDomNode.offsetHeight - height) / 2;
		}
		return {width, height, margin: heightMargin + "px auto"};
	}
}
