import * as React from "react";
import {xhr} from "../util/xhr";
import {ContentData, SampleDemoComponent} from "../component/SampleDemoComponent";
import {AssetData, SampleDemoAssetComponent} from "../component/SampleDemoAssetComponent";

const GAME_JSON_ID = "sitespecial:game.json";

export interface SampleContentData {
	caption: string;
	description: string;
	dir: string;
}

export interface SampleDemoContainerProps {
	selectedTitle: string;
}

export interface SampleDemoContainerState {
	caption: string;
	description: string;
	contentData: ContentData;
	assetDataList: AssetData[];
	selectedAssetData: AssetData;
	selectedFilePath: string;
	selectedFileContent?: string;
}

export class SampleDemoContainer extends React.Component<SampleDemoContainerProps, SampleDemoContainerState> {
	constructor(props: SampleDemoContainerProps) {
		super(props);
		this.initialize().catch((err) => {
			if (err.name === "NotFound") {
				location.href = "/404.html";
			}
			console.warn(err.message);
		});
	}

	setAssetData = (id: string): void => {
		const targetAssets = this.state.assetDataList.filter(content => content.id === id);
		if (targetAssets.length === 0) {
			throw new Error("選択されたファイルは存在しません。id名：" + id);
		}
		const selectedAssetData = targetAssets[0];
		const selectedFilePath = "/demo/content/" + this.props.selectedTitle + "/" + targetAssets[0].path;
		if (targetAssets[0].type === "image") {
			this.setState({selectedAssetData, selectedFilePath, selectedFileContent: undefined});
		} else {
			this.getFileContent(targetAssets[0].path).then((content) => {
				this.setState({selectedAssetData, selectedFilePath, selectedFileContent: content});
			}).catch((err) => {
				console.warn(err.message);
			});
		}
	}

	render() {
		if (this.state === null) {
			return <div></div>;
		}
		return <div>
			<SampleDemoComponent
				caption={ this.state.caption }
				description={ this.state.description }
				contentData={ this.state.contentData }
			/>
			<SampleDemoAssetComponent
				assetDataList={ this.state.assetDataList }
				selectedAssetData={ this.state.selectedAssetData }
				selectedFilePath={ this.state.selectedFilePath }
				selectedFileContent={ this.state.selectedFileContent }
				showFileContent={ this.setAssetData }
			/>
		</div>;
	}

	private async initialize(): Promise<void> {
		const sampleContentData = await this.getSampleContentData();
		const gameJsonSource = await xhr({ url: "/demo/content/" + this.props.selectedTitle + "/game.json" });
		const gameJson = JSON.parse(gameJsonSource);
		const assetIds = Object.keys(gameJson.assets).filter(id => gameJson.assets[id].type !== "audio");
		const assetDataList = assetIds.map(id => {
			const asset = gameJson.assets[id];
			return {id: id, type: asset.type, path: asset.path};
		});
		assetDataList.unshift({id: GAME_JSON_ID, type: "text", path: "game.json"});
		this.setState({
			caption: sampleContentData.caption,
			description: sampleContentData.description,
			contentData: {
				src: "/demo/content/sample-runner.html?title=" + this.props.selectedTitle,
				width: gameJson.width,
				height: gameJson.height
			},
			assetDataList: assetDataList,
			selectedAssetData: assetDataList[0],
			selectedFilePath: "/demo/content/" + this.props.selectedTitle + "/game.json",
			selectedFileContent: gameJsonSource
		});
	}

	private async getSampleContentData(): Promise<SampleContentData> {
		const contentListSource = await xhr({ url: "/demo/content/list.json" });
		const contents: SampleContentData[] = (JSON.parse(contentListSource))["contents"];
		const targets = contents.filter(entry => entry.dir === this.props.selectedTitle);
		if (targets.length === 0) {
			const error = new Error(this.props.selectedTitle + "というコンテンツは存在しません");
			error.name = "NotFound";
			return Promise.reject(error);
		}
		return targets[0];
	}

	private async getFileContent(path: string): Promise<string> {
		return await xhr({url: "/demo/content/" + this.props.selectedTitle + "/" + path});
	}
}
