import * as React from "react";

declare const hljs: any; // highlight.jsをrequireできないため、highlight.js中のグローバル変数を直接使う

const getFileName = (filePath: string): string => {
	const delimiterIndex = filePath.lastIndexOf("/");
	return (delimiterIndex !== -1) ? filePath.slice(delimiterIndex + 1) : filePath;
};

/**
 * デモコンテンツ中で使用されているアセットに関する情報
 */
export interface AssetData {
	id: string;
	path: string;
	type: string;
}

export interface SampleDemoAssetComponentProps {
	assetDataList: AssetData[];
	selectedAssetData: AssetData;
	selectedFilePath?: string;
	selectedFileContent?: string;
	showFileContent: (id: string) => void;
}

export interface SampleDemoAssetComponentState {
	//  いわゆる container (状態を持つ) ではない component だが、isFollowingScroll はこのcomponentのデザインに関する情報なので、state として保持する
	isFollowingScroll: boolean;
}

/**
 * デモコンテンツで使用されているアセット表示用コンポーネント
 */
export class SampleDemoAssetComponent extends React.Component<SampleDemoAssetComponentProps, SampleDemoAssetComponentState> {
	private assetBodyDomNode: HTMLElement|null = null;

	constructor(props: SampleDemoAssetComponentProps) {
		super(props);
		this.state = {
			isFollowingScroll: false
		};
	}

	componentDidMount() {
		// SP表示用アセットリストをスクロールに追従させるcssクラスを付与する
		window.addEventListener("scroll", () => {
			const headerDom = document.getElementById("akashic-page-header");
			if (this.assetBodyDomNode === null || headerDom === null) {
				return;
			}
			const codeViewComponentTop = this.assetBodyDomNode.getBoundingClientRect().top;
			const headerTop = headerDom.offsetHeight;
			const isFollowingScroll = codeViewComponentTop <= headerTop;
			// SP表示用アセットリスト上部がヘッダーの底に当たった時にだけcssクラスを付与する
			if (this.state.isFollowingScroll != null && this.state.isFollowingScroll !== isFollowingScroll) {
				this.setState({
					isFollowingScroll
				});
			}
		});
	}

	render() {
		const selectedFileName = getFileName(this.props.selectedAssetData.path);
		const spFixedClass = this.state.isFollowingScroll ? " SP--fixed" : "";
		const listDom = this.props.assetDataList.map(asset => {
			return <li
				key={ asset.id }
				className={ asset.id === this.props.selectedAssetData.id ? "selected" : "" }
				onClick={ () => { this.props.showFileContent(asset.id); } }>
					{ getFileName(asset.path) }
				</li>;
		});
		let fileContent;
		switch (this.props.selectedAssetData.type) {
			case "script":
			case "text":
				if (this.props.selectedFileContent !== undefined) {
					const codeContentHtml = hljs.highlightAuto(this.props.selectedFileContent, ["js", "json"]).value;
					fileContent = <pre><code dangerouslySetInnerHTML={ {__html: codeContentHtml} }></code></pre>;
				}
				break;
			case "image":
				fileContent = <div><img src={ this.props.selectedFilePath } alt={ this.props.selectedAssetData.id } /></div>;
				break;
			default:
				// akashic側で定義されているコンテンツタイプではないのでDOMの代入は行わない
				fileContent = null;
				break;
		}

		return <section className="asset--body" ref={ node => this.assetBodyDomNode = node }>
			<div className="responsive--width cfix">
				<div className={ "asset--body--list" + spFixedClass }>
					<ul>{ listDom }</ul>
				</div>
				<p className={ "asset--body--name" + spFixedClass }>{ selectedFileName }</p>
				<div className={ "asset--body--item" + spFixedClass }>
					{ fileContent }
				</div>
			</div>
		</section>;
	}
}
