import * as React from "react";
import * as ReactDOM from "react-dom";
import { SampleDemoContainer } from "./container/SampleDemoContainer";
import * as querystring from "querystring";

function parseTitle(): string {
	let title = querystring.parse(location.search.substr(1))["title"];
	if (Array.isArray(title)) {
		title = title[title.length - 1];
	}
	return title === undefined ? "helloworld" : title;
}

ReactDOM.render(
	<SampleDemoContainer selectedTitle={parseTitle()} />,
	document.getElementById("app")
);
